import React, { Component, MouseEvent, UIEvent, ChangeEvent } from "react";
import "./css/LoginPage.scss";
import { Button } from "react-bootstrap";
import { Redirect, useHistory, withRouter, RouteComponentProps } from "react-router-dom";
import { connect } from "react-redux";
import { getPosts } from "../actions/postActions";
import PostItem from "../components/PostItem";
import { loginViaEmailOrSMS } from "../actions/userActions";
import LeftMenu from "../components/LeftMenu";
import Loader from '../components/coreUI/Loader'

type myProps = RouteComponentProps & {};
type myState = { txtUsername: string; txtPassword: string; isLoading: Boolean };
class LoginPage extends Component<myProps, myState> {
  constructor(props: any) {
    super(props);
    this.state = { txtUsername: "", txtPassword: "", isLoading: false };
    this._handleSubmit = this._handleSubmit.bind(this);
    this._handleUsernameChange = this._handleUsernameChange.bind(this);
    this._handlePasswordChange = this._handlePasswordChange.bind(this);
  }
  _handleSubmit(e: MouseEvent) {
    this.setState({ isLoading: true })
    loginViaEmailOrSMS(
      this.state.txtUsername,
      "email",
      "",
      (res: any) => {
        // let hist = useHistory();
        this.setState({ ...this.state, isLoading: false });
        let response = res.data;
        if (response.data.account_type === "existed") {
          //   window.location.href = "/code";
          this.props.history.push("/code/lvdatri@gmail.com");
        } else {
          window.location.href = "/register";
        }
      },
      (err: any) => {
        alert(err);
        this.setState({ ...this.state, isLoading: false });
      }
    );
  }
  _handleUsernameChange(newText: React.ChangeEvent<HTMLInputElement>) {
    this.setState({ txtUsername: newText.target.value });
    console.log("text change", newText);
  }
  _handlePasswordChange(newText: React.ChangeEvent<HTMLInputElement>) {
    this.setState({ txtPassword: newText.target.value });
    console.log("text change", newText);
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-2 kuky-col-left">
            <LeftMenu />
          </div>
          <div className="col-10 kuky-col-right">
            <div className="kuky-main-left"></div>
            <div className="kuky-main-center center">
              {this.state.isLoading ? <Loader /> : <div>
                <div className="kuky-header-1__light top-header">provide us your</div>
                <div className="kuky-header-1__bold">email</div>
                <div>
                  We will send a code so that you can start <br /> contributing to the community
              </div>
                <div className="login-block main-block">
                  <div>
                    <div>Your email</div>
                    <input
                      className="kuky-form-input"
                      type="text"
                      value={this.state.txtUsername}
                      onChange={(e: React.ChangeEvent<HTMLInputElement>) => this._handleUsernameChange(e)}
                      placeholder="enter your email"
                    />
                  </div>
                  <Button className="button-top" onClick={(e: MouseEvent) => this._handleSubmit(e)}>
                    Send me the code
                </Button>
                </div>
              </div>}

            </div>
            <div className="kuky-main-right"></div>
          </div>
        </div>
      </div>
    );
  }
}
const MapStateToProps = (store: any) => ({ user: store.user });
const LoginContainer = connect(MapStateToProps)(LoginPage);
export default withRouter(LoginContainer);
