import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter, RouteComponentProps, Link } from "react-router-dom";
import LeftMenu from "../components/LeftMenu";

type myProps = RouteComponentProps & any;
class LogoutPage extends Component<myProps> {
  componentDidMount() {
    this.props.dispatch({ type: "USER_LOGOUT" });
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-2 kuky-col-left">
            <LeftMenu />
          </div>
          <div className="col-10 kuky-col-right">
            <div className="kuky-main-left"></div>
            <div className="kuky-main-center">
              <Link to="login">go back to login here</Link>
            </div>
            <div className="kuky-main-right"></div>
          </div>
        </div>
      </div>
    );
  }
}
const MapStateToProps = (store: any) => ({ user: store.user });
const LogoutContainer = connect(MapStateToProps)(LogoutPage);
export default withRouter(LogoutContainer);
